@media (max-width: 1200px) {
	.menu {
		&__item {
			padding-right: 19px; }
		&__link {
			font-size: 13px; } }
	.subheader {
		&__tel {
			font-size: 17px;
			margin-top: 3px; } }
	.capabilities {
		&__card {
			width: 300px; } }
	.footer {
		&__menu-top {
			a {
				margin-right: 19px; } } }
	.title-black {
		margin-top: 15px; }
	.title-white {
		margin-top: 15px; }
	.subtitle-black {
		margin-top: 0; }
	.subtitle-white {
		margin-top: 0; } }

@media (max-width: 991px) {
	.menu {
		&__item {
			padding-right: 9px; }
		&__link {
			font-size: 10px; } }
	.subheader {
		&__logo {
			img {
				width: 100%; } }
		&__logo-text {
			font-size: 12px; }
		&__btn {
			width: 92%; } }
	.promo {
		&__header {
			font-size: 42px; }
		&__subheader {
			font-size: 27px;
			margin-top: 11px; }
		&__text {
			font-size: 16px; } }
	.capabilities {
		&__card {
			width: 223px;
			height: 223px;
			&__title {
				font-size: 17px; } } } }

