header {
  position: relative;
  height: 50px; }


.header-top {
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #212121;
  z-index: 2; }


.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  &__link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300;
    text-decoration: none; }

  &__item {
    border-right: 2px dotted black;
    padding-right: 28px; } }

.subheader {
  position: absolute;
  top: 50px;


  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  &__logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  &__logo-text {
    margin-top: 6px;
    opacity: 0.5;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400; }

  &__tel-text {
    color: #ffffff;
    font-size: 12px;
    font-weight: 300;
    margin-top: 19px;
    text-align: right; }

  &__tel {
    display: block;
    margin-top: 8px;
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    line-height: 12px;
    text-decoration: none; }

  &__btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    @include btn_hover; } }

.promo {
  min-height: 800px;
  background: url('../img/bg/bg1.png') center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  &__header {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 60px;
    line-height: 60px;
    margin: 0;
    text-align: center; }

  &__subheader {
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 36px;
    line-height: 36px;
    margin: 0;
    margin-top: 22px;
    text-align: center; }

  &__text {
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    margin-top: 23px; }

  &__btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase;
    @include btn_hover; } }

.reasons {
  padding: 71px 0 65px 0;
  &__column {
    display: flex;
    justify-content: space-between;
    margin-top: 35px;
    &__img {
      min-width: 113px;
      max-height: 113px;
      background-color: #1eacc7;
      border-radius: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 26px; }
    &__top {
      font-size: 17px;
      line-height: 21px;
      font-weight: 400;
      color: #1eacc7;
      margin-right: 26px; }
    &__text {
      width: 80%;
      font-size: 15px;
      font-weight: 300;
      color: #202020;
      margin-right: 26px; } } }




.label-black {
  width: 112px;
  height: 24px;
  background-color: #000000;
  text-align: center;
  line-height: 24px;
  font-size: 11px;
  font-weight: 100;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0px auto; }

.title-black {
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  color: #222222;
  margin-top: 30px; }

.subtitle-black {
  font-size: 17px;
  font-weight: 400;
  color: #222222;
  text-align: center;
  margin-top: 10px; }


.capabilities {
  background: url('../img/bg/bg-capabilities.png') center (center / cover) no-repeat;
  min-height: 864px;
  padding: 48px 0px 80px 0px;
  .row {
    margin-top: 32px; }
  &__card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 320px;
    height: 280px;
    padding: 90px 0 35px 0;
    margin: 0 auto;
    margin-bottom: 19px;
    &__1 {
      background: url('../img/capabilities/cap1.png') center (center / cover) no-repeat; }
    &__2 {
      background: url('../img/capabilities/cap2.png') center (center / cover) no-repeat; }
    &__3 {
      background: url('../img/capabilities/cap3.png') center (center / cover) no-repeat; }
    &__4 {
      background: url('../img/capabilities/cap4.png') center (center / cover) no-repeat; }
    &__5 {
      background: url('../img/capabilities/cap5.png') center (center / cover) no-repeat; }
    &__6 {
      background: url('../img/capabilities/cap6.png') center (center / cover) no-repeat; }
    &__title {
      font-size: 24px;
      line-height: 31px;
      font-weight: 400;
      text-transform: uppercase;
      color: #ffffff;
      text-align: center; }
    &__icon {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 26px;
      height: 26px;
      background-color: #000000;
      border-radius: 50%;
      margin: 0 auto; } } }

.label-white {
  width: 112px;
  height: 24px;
  background-color: #ffffff;
  text-align: center;
  line-height: 24px;
  font-size: 11px;
  font-weight: 100;
  text-transform: uppercase;
  color: #070716;
  margin: 0px auto; }

.title-white {
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  margin-top: 30px; }

.subtitle-white {
  font-size: 17px;
  font-weight: 400;
  color: #ffffff;
  text-align: center;
  margin-top: 10px; }

.choice {
  min-height: 500px;
  padding: 56px 0 138px 0;
  background: url('../img/bg/bg_choice.png') center (center / cover) no-repeat;
  &__img {
    width: 759px;
    height: 500px;
    margin: 0 auto;
    margin-top: 56px; }

  &__text {
    font-size: 17px;
    font-weight: 300;
    color: #222222;
    text-align: center; } }

.conditions {
  min-height: 929px;
  padding: 68px 0 84px 0;
  &__card {
    margin: 30px 0 30px 0; }
  &__title {
    font-size: 22px;
    font-weight: 400;
    color: #1ca8c3;
    margin-bottom: 10px; }
  &__elem {
    margin-bottom: 10px;
    position: relative;
    &:before {
      content: "";
      display: block;
      position: absolute;
      left: -31px;
      top: 2px;
      height: 20px;
      width: 21px;
      background: url('../img/conditions/blue_icon.png') center (center / cover) no-repeat; } }

  &__text {
    font-size: 17px;
    font-weight: 300;
    color: #1ca8c3; }
  &__list {
    list-style-type: none;
    padding-left: 50px;
    font-size: 17px;
    font-weight: 300;
    color: #222222; }
  .red-conditions {
    .conditions__title {
      color:#ff5656 {} }
    .conditions__elem {
      &:before {
        background: url('../img/conditions/red_icon.png') center (center / cover) no-repeat; } } } }


.city {
  min-height: 717px;
  padding: 49px 0;
  background: url('../img/bg/city.png') center (center / cover) no-repeat;
  position: relative;
  &__view {
    text-align: center;
    position: absolute;
    bottom: 49px;
    left: 50%;
    transform: translateX(-50%);
    a {
      font-size: 15px;
      font-weight: 400;
      text-transform: uppercase;
      color: #1fbad6;
      text-decoration: none; } } }

.footer {
  min-height: 156px;
  padding: 14px 23px 39px 23px;
  border-bottom: 8px solid black;
  .line {
      background: #1fbad6;
      height: 4px;
      width: 299px;
      margin: 0 auto; }
  &__main {
    display: flex;
    justify-content: space-between; }
  &__main>div {
      width: 33%;
      &:nth-child(3) {
        display: flex;
        justify-content: flex-end; } }
  &__network {
    display: flex;
    margin-top: 33px;
    &-item {
      height: 32px;
      width: 32px;
      border-radius: 100%;
      background: #070716;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 7px;
      i {
        color: white; } } }
  &__menu-top {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    a {
      font-size: 15px;
      font-weight: 300;
      text-transform: uppercase;
      color: #070716;
      margin-right: 46px; } }
  &__menu-bottom {
    display: flex;
    justify-content: space-around;
    margin: 10px 0 0 0;
    padding: 0;
    a {
      font-size: 11px;
      font-weight: 300;
      text-transform: uppercase;
      color: #999999; } }
  &__lang {
    font-size: 11px;
    font-weight: 400;
    text-transform: uppercase;
    color: #1fbad6;
    margin-top: 19px;
    text-align: center; }


  &__platforms {
    margin: 33px 10px 0 0; } }


